export default {
	data() {
		return {
			query:'',
			userName:'',
			tel:'',
			unitAddress:'',
			location:'请选择您的位置',
			latitude:'',
			longitude:'',
			isChoose:false,
			selfAddress:'请选择自提位置'
		};
	},
	onLoad(obj) {
		this.query = obj.name
	},
	methods:{
		getMap(){
			uni.chooseLocation({
			    success:  res=> {
					console.log(res)
			        this.location = res.address
					this.isChoose = true
					this.latitude = res.latitude
					this.longitude = res.longitude
			    }
			});
		},
		toSelf(){
			uni.navigateTo({
				url:'/pages/newAddress/selfAddress'
			})
		}
	}
}
